:root {
  --white: #f8f8f8;
  --yellow: #eea10c;
  --green: #9bb537;
  --black: #161616;
  --black-05: #050505;
  --grey: #fff;
  --grey-inputs: rgba(32, 32, 31, .035);
  --switch-btn-grey: #cecdcd;
  --green-gradient: #9bb537, rgba(155, 181, 55, .69);
  --img-grad: radial-gradient(circle, var(--green), 10%, #050505);
  --border: none;
  --black-rgba: rgba(5, 5, 5, .5);
  --black-rgba-2: rgba(5, 5, 5, .2);
  --black-rgba-3: rgba(5, 5, 5, .3);
  --black-rgba-still: rgba(5, 5, 5, .5);
  --transition-dur-and-func: 250ms cubic-bezier(.4, 0, .2, 1);
  --modal-text-black: #f8f8f8;
  --green-backdrop: #9bb537;
  --hover-modal-text: #fff;
}

.darkmode {
  --white: #161616;
  --yellow: #eea10c;
  --green: #9bb537;
  --black: #f8f8f8;
  --black-05: #fff;
  --grey: #fff;
  --grey-inputs: rgba(255, 255, 255, .1);
  --switch-btn-grey: #cecdcd;
  --green-gradient: #9bb537, rgba(155, 181, 55, .69);
  --img-grad: radial-gradient(circle, var(--green), 10%, #050505);
  --border: 1px solid var(--green);
  --black-rgba: rgba(255, 255, 255, .5);
  --black-rgba-2: rgba(255, 255, 255, .2);
  --black-rgba-3: rgba(255, 255, 255, .3);
  --black-rgba-still: rgba(5, 5, 5, .5);
  --transition-dur-and-func: 250ms cubic-bezier(.4, 0, .2, 1);
  --modal-text-black: #f8f8f8;
  --green-backdrop: #050505;
  --hover-modal-text: var(--green);
}

