.backdrop {
  z-index: 2147483647;
  visibility: hidden;
  width: 50vw;
  height: 100vh;
  background-color: var(--green-backdrop);
  transition: left .3s;
  position: fixed;
  top: 0;
  left: 0;
}

.modal-text-div {
  text-align: center;
  flex-direction: column;
  gap: 16px;
  margin-top: 200px;
  display: flex;
}

.modal-home {
  color: var(--modal-text-black);
  font-family: Inter;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.modal-home:hover, .modal-home:focus {
  color: var(--hover-modal-text);
  font-weight: 600;
}

.modal-fav {
  color: var(--modal-text-black);
  font-size: 12px;
  font-weight: 400;
}

.modal-fav:hover, .modal-fav:focus {
  color: var(--hover-modal-text);
  font-weight: 600;
}

.cross {
  position: absolute;
  top: 20px;
  right: 20px;
}

.switch {
  width: 46px;
  height: 20px;
  display: inline-block;
  position: relative;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  cursor: pointer;
  background-color: var(--switch-btn-grey);
  border-radius: 34px;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slider:before {
  content: "";
  height: 20.5px;
  width: 20.5px;
  background-color: #fff;
  border-radius: 50%;
  transition: all .4s;
  position: absolute;
  bottom: 0;
  left: 0;
}

input:checked + .slider {
  background-color: var(--green);
}

input:focus + .slider {
  box-shadow: 0 0 1px var(--green);
}

input:checked + .slider:before {
  transform: translateX(26px);
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

.switch {
  display: none;
}

.modal-switch {
  display: block;
  top: 350px;
  left: 20px;
}

